import React, { useState } from "react"
import addToMailchimp from "gatsby-plugin-mailchimp"
import Input from "../Input/input"
import Button from "../Button/button"
import {
  NewsletterWrapper,
  NewsletterInnerWrapper,
  NewsletterTitle,
  NewsletterDescription,
  NewsletterInputWrapper,
  ErrorMessage,
  SuccessMessage,
} from "./newsletter.style"

type NewsletterProps = {}

const Newsletter: React.FunctionComponent<NewsletterProps> = ({ ...props }) => {
  const [email, setEmail] = useState("")
  const [success, setSuccess] = useState()
  const [error, setError] = useState()
  const handleChange = (e: any) => {
    setEmail(e.target.value)
  }

  const handleSubmit = (e: any) => {
    e.preventDefault()
    addToMailchimp(email) // listFields are optional if you are only capturing the email address.
      .then(({ msg, result }: any) => {
        if (result !== "success") {
          throw msg
        }
        setSuccess(msg)
        setError("")
        setEmail("")
      })
      .catch((err: any) => {
        setError(err)
        setSuccess("")
        setEmail("")
      })
  }

  return (
    null
    // <NewsletterWrapper {...props}>
    //   <NewsletterInnerWrapper>
    //     <NewsletterTitle>
    //       Recibe en tu correo los √∫ltimos art√≠culos.
    //     </NewsletterTitle>
    //     <NewsletterDescription>
    //       Suscr√≠bete y mantente al tanto.
    //     </NewsletterDescription>

    //     <NewsletterInputWrapper onSubmit={handleSubmit}>
    //       {success ? (
    //         <SuccessMessage>{success} üôÇ</SuccessMessage>
    //       ) : (
    //           <>
    //             <Input
    //               type="email"
    //               name="email"
    //               placeholder="T√∫ correo electr√≥nico aqu√≠"
    //               onChange={handleChange}
    //               value={email}
    //               required
    //             />
    //             <Button title="Suscribirme" type="submit" />
    //           </>
    //         )}
    //     </NewsletterInputWrapper>
    //     {error && (
    //       <ErrorMessage
    //         dangerouslySetInnerHTML={{ __html: `<span>*</span>${error}` }}
    //       />
    //     )}
    //   </NewsletterInnerWrapper>
    // </NewsletterWrapper>
  )
}

export default Newsletter
